'use strict'

import event from './event'

const MODE_NONE = 'none'
const MODE_UNDO = 'undo'
const MODE_REDO = 'redo'
let history = []
let historyIndex = -1
let mode = MODE_NONE

const History = {
  start: function () {
    const self = this
    event.on('editor:undo', () => {
      self.undo()
    })
    event.on('editor:redo', () => {
      self.redo()
    })
  },

  reset: function () {
    history = []
  },

  push: function (context, undo, redo) {
    if (mode === MODE_NONE) {
      for (let i = historyIndex + 1; i < history.length; i++) {
        history.pop()
      }
      history.push({ context, undo, redo })
      historyIndex++
    }
  },

  unpush: function () {
  },

  undo: function () {
    const index = historyIndex
    if (index !== undefined && index >= 0) {
      historyIndex--
      mode = MODE_UNDO
      history[index].undo.call(history[index].context)
      mode = MODE_NONE
    }
  },

  redo: function () {
    const index = historyIndex + 1
    if (index !== undefined && index < history.length) {
      mode = MODE_REDO
      history[index].redo.call(history[index].context)
      mode = MODE_NONE
      historyIndex++
    }
  }
}

export default History
