'use strict'

import event from './event'
import { EDITOR_KEYS_REDO, EDITOR_KEYS_UNDO, EDITOR_KEYS_CANCEL } from '../constant'

class Keys {
  triggers = []
  pressedKeys = []

  constructor () {
    this.triggers.push({ event: 'editor:redo', keys: EDITOR_KEYS_REDO })
    this.triggers.push({ event: 'editor:undo', keys: EDITOR_KEYS_UNDO })
    this.triggers.push({ event: 'editor:cancel', keys: EDITOR_KEYS_CANCEL })
    this.initKeyboardEvents()
  }

  initKeyboardEvents () {
    const self = this
    document.onkeydown = function (e) {
      const index = self.pressedKeys.indexOf(e.code)
      if (index === -1) {
        self.pressedKeys.push(e.code)
      }
      self.checkTriggers()
    }
    document.onkeyup = function (e) {
      const index = self.pressedKeys.indexOf(e.code)
      if (index !== -1) {
        self.pressedKeys.splice(index, 1)
      }
    }
  }

  checkTriggers () {
    for (let i = 0; i < this.triggers.length; i++) {
      const trigger = this.triggers[i]
      if (this.isAllKeysPressed(trigger.keys)) {
        event.broadcast(trigger.event)
        return
      }
    }
  }

  isAllKeysPressed (codes) {
    let nbPressed = 0
    for (let i = 0; i < codes.length; i++) {
      if (this.isKeyPressed(codes[i])) {
        nbPressed++
      }
    }

    return nbPressed === codes.length
  }

  isKeyPressed (code) {
    return this.pressedKeys.indexOf(code) !== -1
  }
}

export default Keys
