'use strict'

import { TILES_PER_LINE, TILES_PER_COLUMN, TILE_WIDTH, TILE_HEIGHT, MAP_TYPE_WORLD } from '../constant'
import { Map } from '../model/map'
import { Tile } from '../model/tile'
import resource from '../resource'
import HitboxManager from './hitbox-manager'
import { TileTransition } from '../model/tile-transition'

export default {
  world: [],
  caverns: [],
  level1: [],
  level2: [],
  level3: [],
  level4: [],
  level5: [],
  level6: [],
  level7: [],
  level8: [],
  level9: [],

  createMaps (data) {
    this.world = this.createMapsForType('world', data.world)
    this.caverns = this.createMapsForType('caverns', data.caverns)
    this.level1 = this.createMapsForType('level1', data.level1)
  },

  createMapsForType (type, mapsData) {
    const maps = []
    for (let i = 0; i < mapsData.length; i++) {
      const mapData = mapsData[i]
      const column = mapData.x
      const line = mapData.y
      const width = TILES_PER_LINE * TILE_WIDTH
      const height = TILES_PER_COLUMN * TILE_HEIGHT
      const x = column * width
      const y = line * height
      const tiles = this.createMapTiles(mapData.tiles, x, y)
      if (!maps[column]) {
        maps[column] = []
      }
      maps[column][line] = new Map(column, line, x, y, width, height, type, tiles, [/* creatures */], [/* items */], [/* characters */], [/* miscs */])
    }

    return maps
  },

  createMapTiles (tilesData, offsetX, offsetY) {
    const tiles = []
    for (let i = 0; i < tilesData.length; i++) {
      const tileData = tilesData[i]
      const column = tileData.x
      const line = tileData.y
      const width = TILE_WIDTH
      const height = TILE_HEIGHT
      const x = column * width + offsetX
      const y = line * height + offsetY
      const sprite = resource.getSprite(tileData.sprite)
      const block = HitboxManager.getHitboxFromString(x, y, width, height, tileData.hitbox)
      const transition = new TileTransition(tileData.transition?.start, tileData.transition?.targetMapType, tileData.transition?.targetMapColumn, tileData.transition?.targetMapLine, tileData.transition?.targetTileColumn, tileData.transition?.targetTileLine, tileData.transition?.end)
      if (!tiles[column]) {
        tiles[column] = []
      }
      tiles[column][line] = new Tile(column, line, x, y, width, height, tileData.hitbox, sprite, block, undefined, transition, !!tileData.start)
    }

    return tiles
  },

  getMap (type, column, line) {
    return this[type] && this[type][column] && this[type][column][line] ? this[type][column][line] : undefined
  },

  findStartingPosition () {
    for (const map of this.mapsIterator(MAP_TYPE_WORLD)) {
      for (const tile of map.tilesIterator()) {
        if (tile.start) {
          return {
            map,
            tileColumn: tile.column,
            tileLine: tile.line
          }
        }
      }
    }
  },

  * mapsIterator (type) {
    for (let column = 0; column < this[type].length; column++) {
      for (let line = 0; line < this[type][column].length; line++) {
        yield this[type][column][line]
      }
    }
  }
}
