'use strict'

import AbstractPanel from 'Panel/abstract-panel'
import { Tile } from '../../model/tile'
import event from '../event'
import { TILES_PATH, TILE_WIDTH, TILE_HEIGHT } from '../../constant'
import io from '../../utils/io'
import resource from '../../resource'
import { TileTransition } from '../../model/tile-transition'

class TileSelectorPanel extends AbstractPanel {
  canvas
  context

  worldTiles = []
  cavernsTiles = []
  level1Tiles = []

  selectedColor = 'green'
  hoverColor = 'red'
  color = 'black'
  fillRule = 'nonzero'
  tiles = []

  constructor (element, container, minWidth, minHeight, maxWidth, maxHeight) {
    super(element, container, minWidth, minHeight, maxWidth, maxHeight)

    this.canvas = element.querySelector('canvas')
    this.context = this.canvas.getContext('2d')
  }

  start () {
    const self = this
    this.canvas.addEventListener('mousemove', e => {
      const mouseTile = self.getMouseTile(e.offsetX, e.offsetY)
      self.hover(mouseTile)
    })
    this.canvas.addEventListener('mouseout', e => {
      self.hover()
    })
    this.canvas.addEventListener('click', e => {
      const mouseTile = self.getMouseTile(e.offsetX, e.offsetY)
      self.toggleTileSelection(mouseTile)
    })

    event.on('menu:map-selector:display-world', () => {
      self.tiles = self.worldTiles
      self.resyncTiles()
      self.draw()
    })
    event.on('menu:map-selector:display-caverns', () => {
      self.tiles = self.cavernsTiles
      self.resyncTiles()
      self.draw()
    })
    event.on('menu:map-selector:display-level1', () => {
      self.tiles = self.level1Tiles
      self.resyncTiles()
      self.draw()
    })

    event.on('menu:map-selector:display-world', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-caverns', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level1', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level2', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level3', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level4', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level5', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level6', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level7', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level8', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
    event.on('menu:map-selector:display-level9', () => {
      event.broadcast('tile-selector-panel:unselect')
    })

    event.on('editor:cancel', () => {
      event.broadcast('tile-selector-panel:unselect')
    })
  }

  hover (tile) {
    for (let i = 0; i < this.tiles.length; i++) {
      if (this.tiles[i].tilePath) {
        this.tiles[i].hover = false
      }
    }
    if (tile) {
      tile.hover = true
    }
    this.draw()
  }

  toggleTileSelection (mouseTile) {
    for (let i = 0; i < this.tiles.length; i++) {
      const tile = this.tiles[i]
      if (mouseTile === tile) {
        tile.selected = !tile.selected
        if (tile.selected) {
          event.broadcast('tile-selector-panel:select', tile)
        } else {
          event.broadcast('tile-selector-panel:unselect')
        }
      } else {
        tile.selected = false
      }
    }
    this.draw()
  }

  unselectAll () {
    for (let i = 0; i < this.tiles.length; i++) {
      const tile = this.tiles[i]
      tile.selected = false
    }
    this.draw()
  }

  getMouseTile (mouseX, mouseY) {
    for (let i = 0; i < this.tiles.length; i++) {
      const tile = this.tiles[i]
      if (this.isPointInPath(tile.tilePath, mouseX, mouseY)) {
        return tile
      }
    }

    return undefined
  }

  draw () {
    this.context.fillStyle = this.color
    this.context.fillRect(0, 0, this.canvas.width, this.canvas.height)

    for (let i = 0; i < this.tiles.length; i++) {
      this.tiles[i].draw(this.context)
    }

    for (let i = 0; i < this.tiles.length; i++) {
      this.tiles[i].draw(this.context)
      this.context.strokeStyle = this.getStrokeColor(this.tiles[i])
      this.context.stroke(this.tiles[i].tilePath)
    }
  }

  getStrokeColor (sprite) {
    if (sprite.selected) {
      return this.selectedColor
    }
    if (sprite.hover) {
      return this.hoverColor
    }
    return this.color
  }

  async loadTiles () {
    const tilesData = await io.fetchRemoteFile(TILES_PATH)
    for (let i = 0; i < tilesData.world.length; i++) {
      const tileData = tilesData.world[i]
      const sprite = resource.getSprite(tileData.sprite)
      const block = tileData.block
      const tile = new Tile(0, 0, 0, 0, TILE_WIDTH, TILE_HEIGHT, block, sprite.clone(), null, null, new TileTransition())
      this.worldTiles.push(tile)
    }
    for (let i = 0; i < tilesData.caverns.length; i++) {
      const tileData = tilesData.caverns[i]
      const sprite = resource.getSprite(tileData.sprite)
      const block = tileData.block
      const tile = new Tile(0, 0, 0, 0, TILE_WIDTH, TILE_HEIGHT, block, sprite.clone(), null, null, new TileTransition())
      this.cavernsTiles.push(tile)
    }
    for (let i = 0; i < tilesData.level1.length; i++) {
      const tileData = tilesData.level1[i]
      const sprite = resource.getSprite(tileData.sprite)
      const block = tileData.block
      const tile = new Tile(0, 0, 0, 0, TILE_WIDTH, TILE_HEIGHT, block, sprite.clone(), null, null, new TileTransition())
      this.level1Tiles.push(tile)
    }

    this.tiles = this.worldTiles

    this.resyncTiles()
  }

  resyncTiles () {
    let x = 0
    let y = 0
    let isOutOfBounds = false
    for (let i = 0; i < this.tiles.length; i++) {
      const tile = this.tiles[i]
      if (isOutOfBounds) {
        tile.x = -TILE_WIDTH
        tile.y = -TILE_HEIGHT
        continue
      }
      tile.x = x
      tile.y = y
      const path2D = new Path2D()
      path2D.rect(x, y, TILE_WIDTH, TILE_HEIGHT)
      tile.tilePath = path2D

      x += TILE_WIDTH
      if (x + TILE_WIDTH > this.canvas.width) {
        x = 0
        y += TILE_HEIGHT
        if (y + TILE_HEIGHT > this.canvas.height) {
          y = 0
          isOutOfBounds = true
        }
      }
    }
  }

  isPointInPath (path2D, x, y) {
    // this.context.translate(this.canvas.width / 2, this.canvas.height / 2)
    // this.context.scale(this.scale, this.scale)
    // this.context.translate(-this.canvas.width / 2, -this.canvas.height / 2)
    // this.context.translate(this.translateX, this.translateY)

    const isInPath = this.context.isPointInPath(path2D, x, y, this.fillRule)

    // this.context.restore()
    // this.context.setTransform(1, 0, 0, 1, 0, 0)

    return isInPath
  }

  resizePanel (element, event) {
    super.resizePanel(element, event)
    this.resyncTiles()
    this.draw()
  }
}

export default TileSelectorPanel
