'use strict'

import event from '../event'

class GeneralMenu {
  constructor (
    importOldWorldJsonButton,
    importWorldJsonButton,
    importDefaultWorldLozButton,
    importWorldLozButton,
    exportWorldJsonButton,
    exportWorldLozButton,
    toggleBlockButton
  ) {
    importOldWorldJsonButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:general:import-old-world-json')
    })
    importWorldJsonButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:general:import-world-json')
    })
    importDefaultWorldLozButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:general:import-default-world-loz')
    })
    importWorldLozButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:general:import-world-loz')
    })
    exportWorldJsonButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:general:export-world-json')
    })
    exportWorldLozButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:general:export-world-loz')
    })
    toggleBlockButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:general:toggle-block')
    })
  }
}

export default GeneralMenu
