'use strict'

import event from '../event'

class MapSelectorMenu {
  constructor (
    displayWorldButton,
    displayCavernsButton,
    displayLevel1Button,
    displayLevel2Button,
    displayLevel3Button,
    displayLevel4Button,
    displayLevel5Button,
    displayLevel6Button,
    displayLevel7Button,
    displayLevel8Button,
    displayLevel9Button
  ) {
    displayWorldButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-world')
    })
    displayCavernsButton.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-caverns')
    })
    displayLevel1Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level1')
    })
    displayLevel2Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level2')
    })
    displayLevel3Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level3')
    })
    displayLevel4Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level4')
    })
    displayLevel5Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level5')
    })
    displayLevel6Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level6')
    })
    displayLevel7Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level7')
    })
    displayLevel8Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level8')
    })
    displayLevel9Button.addEventListener('click', e => {
      e.preventDefault()
      event.broadcast('menu:map-selector:display-level9')
    })
  }
}

export default MapSelectorMenu
