'use strict'

import AbstractPanel from 'Panel/abstract-panel'
import { Tile } from '../../model/tile'
import event from '../event'
import { MAP_TYPE_DUNGEON, MAP_TYPE_CAVERN, PATH_DATA_GAME, FILE_TYPE_JSON, FILE_TYPE_LOZ, TILE_WIDTH, TILE_HEIGHT, TILES_PER_LINE, TILES_PER_COLUMN, WORLD_MAPS_PER_LINE, WORLD_MAPS_PER_COLUMN, DUNGEON_MAPS_PER_LINE, DUNGEON_MAPS_PER_COLUMN, PATH_DATA_EMPTY_WORLD, MAP_TYPE_WORLD } from '../../constant'
import history from '../history'
import io from '../../utils/io'
import resource from '../../resource'
import { TileTransition } from '../../model/tile-transition'

const DISPLAY_WORLD = 'world'
const DISPLAY_CAVERNS = 'caverns'
const DISPLAY_LEVEL1 = 'level1'
const MODE_NONE = 'none'
const MODE_DRAG = 'drag'
const MODE_SPRITE = 'stripe'

class WorldPanel extends AbstractPanel {
  canvas
  context

  worldTiles = []
  cavernsTiles = []
  level1Tiles = []
  level2Tiles = []
  level3Tiles = []
  level4Tiles = []
  level5Tiles = []
  level6Tiles = []
  level7Tiles = []
  level8Tiles = []
  level9Tiles = []

  tiles = []
  selectedColor = 'green'
  hoverColor = 'red'
  color = 'black'
  fillRule = 'nonzero'
  scale = 1.0
  scaleMultiplier = 0.02
  maxScale = 1.0
  minScale = 0.22
  translateX = 0
  translateY = 0
  translateMultiplier = 10
  drawBlock = false
  mode = MODE_NONE
  display = DISPLAY_WORLD

  constructor (element, container, minWidth, minHeight, maxWidth, maxHeight) {
    super(element, container, minWidth, minHeight, maxWidth, maxHeight)

    this.canvas = element.querySelector('canvas')
    this.context = this.canvas.getContext('2d')
  }

  start () {
    const self = this
    let mouseDown = false
    this.canvas.addEventListener('mousedown', e => {
      mouseDown = true
      if (self.mode === MODE_NONE) {
        self.mode = MODE_DRAG
      }
    })
    this.canvas.addEventListener('mouseup', e => {
      mouseDown = false
      if (self.mode === MODE_DRAG) {
        self.mode = MODE_NONE
      }
    })
    this.canvas.addEventListener('mousein', e => {
      if (self.mode === MODE_DRAG) {
        self.mode === MODE_NONE
      }
    })
    this.canvas.addEventListener('mouseout', e => {
      if (self.mode === MODE_DRAG) {
        self.mode === MODE_NONE
      }
      if (self.mode === MODE_NONE) {
        self.hover()
      }
    })
    this.canvas.addEventListener('mousemove', e => {
      const mouseTile = self.getMouseTile(e.offsetX, e.offsetY)
      if (self.mode === MODE_SPRITE && !!mouseDown) {
        const newTile = self.applyTile(mouseTile, self.selectedTile.clone())
        event.broadcast('world-panel:select-tile', newTile)
        return
      }
      if (self.mode === MODE_DRAG) {
        self.translate(e.movementX, e.movementY)
        return
      }
      if (self.mode === MODE_NONE || !mouseDown) {
        self.hover(mouseTile)
      }
    })
    this.canvas.addEventListener('click', e => {
      const mouseTile = self.getMouseTile(e.offsetX, e.offsetY)
      if (self.mode === MODE_SPRITE) {
        const newTile = self.applyTile(mouseTile, self.selectedTile.clone())
        event.broadcast('world-panel:select-tile', newTile)
        return
      }
      if (self.mode === MODE_DRAG) {
        return
      }
      if (self.mode === MODE_NONE) {
        self.select(mouseTile)
        event.broadcast('world-panel:select-tile', mouseTile)
      }
    })
    this.canvas.addEventListener('wheel', e => {
      self.zoom(e.deltaY < 0)
    })

    event.on('menu:map-selector:display-world', () => {
      self.tiles = self.worldTiles
      self.display = DISPLAY_WORLD
      self.draw()
    })
    event.on('menu:map-selector:display-caverns', () => {
      self.tiles = self.cavernsTiles
      self.display = DISPLAY_CAVERNS
      self.draw()
    })
    event.on('menu:map-selector:display-level1', () => {
      self.tiles = self.level1Tiles
      self.display = DISPLAY_LEVEL1
      self.draw()
    })
    event.on('menu:general:import-old-world-json', async () => {
      const data = await io.openFile(FILE_TYPE_JSON)
      self.importOldWorld(data)
    })
    event.on('menu:general:import-world-json', async () => {
      const data = await io.openFile(FILE_TYPE_JSON)
      self.importWorld(data)
    })
    event.on('menu:general:import-default-world-loz', async () => {
      const data = await io.fetchRemoteFile(PATH_DATA_GAME)
      self.importWorld(data)
    })
    event.on('menu:general:import-world-loz', async () => {
      const data = await io.openFile(FILE_TYPE_LOZ)
      self.importWorld(data)
    })
    event.on('menu:general:export-world-json', async () => {
      const data = JSON.stringify(self.exportWorld())
      await io.dowloadFile(data, FILE_TYPE_JSON)
    })
    event.on('menu:general:export-world-loz', async () => {
      const data = JSON.stringify(self.exportWorld())
      await io.dowloadFile(data, FILE_TYPE_LOZ)
    })
    event.on('menu:general:toggle-block', () => {
      self.toggleDrawBlock()
    })

    event.on('tile-selector-panel:select', tile => {
      self.selectedTile = tile
      self.mode = MODE_SPRITE
    })
    event.on('tile-selector-panel:unselect', () => {
      self.selectedTile = undefined
      self.mode = MODE_NONE
    })
    event.on('editor:cancel', () => {
      self.select()
    })
    event.on('tile:starting-position:select', (tile) => {
      self.selectStartingPosition(tile)
    })
  }

  selectStartingPosition (tile) {
    for (let i = 0; i < this.tiles.length; i++) {
      this.tiles[i].start = (this.tiles[i] === tile)
    }
  }

  toggleDrawBlock () {
    this.drawBlock = !this.drawBlock
    this.draw()
  }

  getMouseTile (mouseX, mouseY) {
    for (let i = 0; i < this.tiles.length; i++) {
      const tile = this.tiles[i]
      if (tile.tilePath && this.isPointInPath(tile.tilePath, mouseX, mouseY)) {
        return tile
      }
    }

    return undefined
  }

  zoom (zoomIn) {
    const multiplier = zoomIn ? this.scaleMultiplier : -this.scaleMultiplier
    const scaleIncrement = +(multiplier).toFixed(2)
    let newScale = this.scale + scaleIncrement
    newScale = Math.min(newScale, this.maxScale)
    newScale = Math.max(newScale, this.minScale)
    this.scale = newScale
    this.draw()
  }

  translate (movementX, movementY) {
    this.translateX += (movementX / this.scale)
    this.translateY += (movementY / this.scale)
    this.draw()
  }

  hover (tile) {
    for (let i = 0; i < this.tiles.length; i++) {
      if (this.tiles[i].tilePath) {
        this.tiles[i].hover = false
      }
    }
    if (tile) {
      tile.hover = true
    }
    this.draw()
  }

  select (tile) {
    for (let i = 0; i < this.tiles.length; i++) {
      if (this.tiles[i].tilePath) {
        this.tiles[i].selected = false
      }
    }
    if (tile) {
      tile.selected = true
    }
    this.draw()
  }

  draw () {
    this.context.fillStyle = this.color
    this.context.fillRect(0, 0, this.canvas.width, this.canvas.height)
    this.context.translate(this.canvas.width / 2, this.canvas.height / 2)
    this.context.scale(this.scale, this.scale)
    this.context.translate(-this.canvas.width / 2, -this.canvas.height / 2)
    this.context.translate(this.translateX, this.translateY)

    // Tiles
    for (let i = 0; i < this.tiles.length; i++) {
      const tile = this.tiles[i]
      tile.draw(this.context)

      if (this.drawBlock && !tile.selected && !tile.hover) {
        this.context.save()
        this.context.globalAlpha = 0.5
        this.context.fillStyle = 'red'
        this.context.fill(tile.blockPath)
        this.context.restore()
      }
    }

    // Maps limit
    const mapWidth = TILES_PER_LINE * TILE_WIDTH
    const mapHeight = TILES_PER_COLUMN * TILE_HEIGHT
    const nbMapPerLine = (this.display === DISPLAY_WORLD || this.display === DISPLAY_CAVERNS) ? WORLD_MAPS_PER_LINE : DUNGEON_MAPS_PER_LINE
    const nbMapPerColumn = (this.display === DISPLAY_WORLD || this.display === DISPLAY_CAVERNS) ? WORLD_MAPS_PER_COLUMN : DUNGEON_MAPS_PER_COLUMN
    for (let x = 0; x < nbMapPerLine * mapWidth; x += mapWidth) {
      for (let y = 0; y < nbMapPerColumn * mapHeight; y += mapHeight) {
        this.context.strokeStyle = 'grey'
        this.context.lineWidth = 4
        this.context.strokeRect(x, y, mapWidth, mapHeight)
        this.context.lineWidth = 1
      }
    }

    // Tiles
    for (let i = 0; i < this.tiles.length; i++) {
      const tile = this.tiles[i]
      this.context.strokeStyle = this.getStrokeColor(tile)
      this.context.stroke(tile.tilePath)
    }

    // Selected/hover tile
    this.context.save()
    for (let i = 0; i < this.tiles.length; i++) {
      const tile = this.tiles[i]
      this.context.globalAlpha = 0.5
      this.context.fillStyle = this.getStrokeColor(tile)
      if (tile.selected) {
        this.context.fill(tile.tilePath)
      }
      if (tile.hover) {
        this.context.fill(tile.tilePath)
      }
    }
    this.context.restore()

    this.context.setTransform(1, 0, 0, 1, 0, 0)
  }

  isPointInPath (path2D, x, y) {
    this.context.translate(this.canvas.width / 2, this.canvas.height / 2)
    this.context.scale(this.scale, this.scale)
    this.context.translate(-this.canvas.width / 2, -this.canvas.height / 2)
    this.context.translate(this.translateX, this.translateY)

    const isInPath = this.context.isPointInPath(path2D, x, y, this.fillRule)

    this.context.restore()
    this.context.setTransform(1, 0, 0, 1, 0, 0)

    return isInPath
  }

  getStrokeColor (tile) {
    if (tile.selected) {
      return this.selectedColor
    }
    if (tile.hover) {
      return this.hoverColor
    }
    return this.color
  }

  applyTile (oldTile, newTile) {
    if (!oldTile || !newTile || oldTile === newTile) {
      return
    }

    const index = this.getTileIndex(oldTile)

    const tile = new Tile(oldTile.column, oldTile.line, oldTile.x, oldTile.y, oldTile.width, oldTile.height, newTile.hitbox, newTile.sprite.clone(), newTile.block, newTile.desctructible, newTile.tileTransition.clone())
    const tilePath = new Path2D()
    tilePath.rect(tile.x, tile.y, tile.width, tile.height)
    tile.tilePath = tilePath

    history.push(this, () => this.applyTile(tile.clone(), oldTile.clone()), () => this.applyTile(oldTile.clone(), tile.clone()))
    this.tiles[index] = tile

    this.draw()

    return tile
  }

  getTileIndex (tile) {
    for (let i = 0; i < this.tiles.length; i++) {
      if (this.tiles[i].x === tile.x && this.tiles[i].y === tile.y) {
        return i
      }
    }
    return undefined
  }

  importOldWorld (world) {
    this.worldTiles = []
    world.sort(function (mapA, mapB) {
      // return (mapA.x * WORLD_MAPS_PER_LINE + mapA.y * WORLD_MAPS_PER_COLUMN) - (mapB.x * WORLD_MAPS_PER_LINE + mapB.y * WORLD_MAPS_PER_COLUMN)
      // return (mapB.x + (WORLD_MAPS_PER_LINE * TILES_PER_LINE * TILE_WIDTH) * mapB.y) - (mapA.x + (WORLD_MAPS_PER_LINE * TILES_PER_LINE * TILE_WIDTH) * mapA.y)
      return (mapA.x + (WORLD_MAPS_PER_LINE * TILES_PER_LINE * TILE_WIDTH) * mapA.y) - (mapB.x + (WORLD_MAPS_PER_LINE * TILES_PER_LINE * TILE_WIDTH) * mapB.y)
    })
    const nb = 16
    for (let i = 0; i < world.length; i++) {
      const map = world[i]
      if (map.t !== 'overworld') {
        continue
      }
      const loaded = []
      for (let j = 0; j < map.tiles.length; j++) {
        const currentTile = map.tiles[j]
        const key = `${map.x}_${map.y}_${currentTile.x}_${currentTile.y}`
        if (loaded.indexOf(key) !== -1) {
          console.log(key)
          continue
        }
        const sprite = resource.getSprite(currentTile.s)
        const tile = new Tile(currentTile.column, currentTile.line, currentTile.x + map.x, currentTile.y + map.y, TILE_WIDTH, TILE_HEIGHT, currentTile.b, sprite)
        this.worldTiles.push(tile)
        loaded.push(key)
      }
    }

    this.tiles = this.worldTiles
    event.broadcast('menu:map-selector:display-world')

    history.reset()

    this.draw()
  }

  importWorld (data) {
    this.level1Tiles = this.importTiles('level1', data, DUNGEON_MAPS_PER_LINE, DUNGEON_MAPS_PER_COLUMN)
    this.cavernsTiles = this.importTiles('caverns', data, WORLD_MAPS_PER_LINE, WORLD_MAPS_PER_COLUMN)
    this.worldTiles = this.importTiles('world', data, WORLD_MAPS_PER_LINE, WORLD_MAPS_PER_COLUMN)

    this.tiles = this.worldTiles
    event.broadcast('menu:map-selector:display-world')

    history.reset()

    this.draw()
  }

  importTiles (type, data, maxMapsPerLine, maxMapsPerColumn) {
    const tiles = []
    for (let i = 0; i < data[type].length; i++) {
      const map = data[type][i]
      if (map.x >= maxMapsPerLine || map.y >= maxMapsPerColumn) {
        continue
      }
      const mapOffsetX = map.x * TILES_PER_LINE * TILE_WIDTH
      const mapOffsetY = map.y * TILES_PER_COLUMN * TILE_HEIGHT
      for (let j = 0; j < map.tiles.length; j++) {
        const currentTile = map.tiles[j]
        const tileOffsetX = currentTile.x * TILE_WIDTH
        const tileOffsetY = currentTile.y * TILE_HEIGHT
        const sprite = resource.getSprite(currentTile.sprite)
        let desctructible
        const transition = new TileTransition(currentTile.transition?.start, currentTile.transition?.targetMapType, currentTile.transition?.targetMapColumn, currentTile.transition?.targetMapLine, currentTile.transition?.targetTileColumn, currentTile.transition?.targetTileLine, currentTile.transition?.end)
        const tile = new Tile(currentTile.x, currentTile.y, mapOffsetX + tileOffsetX, mapOffsetY + tileOffsetY, TILE_WIDTH, TILE_HEIGHT, currentTile.hitbox, sprite, null, desctructible, transition, !!currentTile.start)
        tiles.push(tile)
      }
    }

    return tiles
  }

  async importEmptyWorld () {
    const emptyWorld = await io.fetchRemoteFile(PATH_DATA_EMPTY_WORLD)
    this.importWorld(emptyWorld)
  }

  exportWorld () {
    const world = this.exportTiles(this.worldTiles, WORLD_MAPS_PER_LINE, WORLD_MAPS_PER_COLUMN)
    const caverns = this.exportTiles(this.cavernsTiles, WORLD_MAPS_PER_LINE, WORLD_MAPS_PER_COLUMN)
    const level1 = this.exportTiles(this.level1Tiles, DUNGEON_MAPS_PER_LINE, DUNGEON_MAPS_PER_COLUMN)

    return {
      world,
      caverns,
      level1
    }
  }

  exportTiles (tiles, maxMapsPerLine, maxMapsPerColumn) {
    const maps = []
    for (let x = 0; x < maxMapsPerLine; x++) {
      for (let y = 0; y < maxMapsPerColumn; y++) {
        maps.push({
          x,
          y,
          tiles: [],
          tunnels: [],
          secrets: [],
          creatures: [],
          sprites: []
        })
        const map = maps.at(-1)
        const mapOffsetX = x * TILES_PER_LINE * TILE_WIDTH
        const mapOffsetY = y * TILES_PER_COLUMN * TILE_HEIGHT
        const nbTiles = TILES_PER_LINE * TILES_PER_COLUMN
        for (let k = (maps.length - 1) * nbTiles; k < (maps.length) * nbTiles; k++) {
          const tile = tiles[k]
          if (!tile) {
            return maps
          }
          const tileData = {}
          tileData.x = (tile.x - mapOffsetX) / tile.width
          tileData.y = (tile.y - mapOffsetY) / tile.height
          tileData.hitbox = tile.hitbox
          tileData.sprite = tile.sprite.name
          if (!tile.tileTransition.isEmpty()) {
            tileData.transition = {
              start: tile.tileTransition.start,
              targetMapType: tile.tileTransition.targetMapType,
              targetMapColumn: tile.tileTransition.targetMapColumn,
              targetMapLine: tile.tileTransition.targetMapLine,
              targetTileColumn: tile.tileTransition.targetTileColumn,
              targetTileLine: tile.tileTransition.targetTileLine,
              end: tile.tileTransition.end
            }
          }
          if (tile.start) {
            tileData.start = true
          }

          map.tiles.push(tileData)
        }
      }
    }

    return maps
  }

  resizePanel (element, event) {
    super.resizePanel(element, event)
  }
}

export default WorldPanel
